.spage-contfluid {
  position: relative;
  min-height: 500px;
  min-width: 230px;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  background: url("../images/servicii-it/header-top-bg.png"), url("../images/servicii-it/footer-bottom-bg.png");
  background-position: top center, bottom center;
  background-repeat: no-repeat;
  background-size: 100% 3%, 2000px 14%;
}

.spage-cont-wrapcontent {
  min-height: 500px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding: 100px 5px 200px 5px;
}

.spage-wrpcnt-maintitle {
  position: relative;
  font-family: IndustryNormal;
  font-size: 30px;
  font-weight: bold;
  font-stretch: normal;
  font-style: italic;
  line-height: 1.2;
  letter-spacing: normal;
  text-align: center;
  color: #222053;
  padding-bottom: 10px;
}
.spage-wrpcnt-maintitle:after {
  position: absolute;
  content: "";
  height: 4px;
  width: 320px;
  background-color: #e3aa26;
  bottom: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.spage-wrpcnt-wrplogo {
  width: 450px;
  height: 130px;
  margin: 20px 0 20px 0;
}

.spage-wrpcnt-deftitle {
