  font-size: 26px;
  font-weight: bold;
  font-stretch: normal;
  font-style: italic;
  line-height: 1.2;
  letter-spacing: normal;
  text-align: center;
  color: #222053;
}

.spage-wrpcnt-midtxt {
  width: 70%;
  margin-top: 10px;
  font-family: AcuminProNormal;
  font-size: 18px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.44;
  letter-spacing: normal;
  text-align: center;
