      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  text-align: center;
  margin-top: 50px;
}
.spage-wrpcnt-itsuportrapidtitle h5 {
  font-family: IndustryNormal;
  font-size: 21px;
  font-weight: bold;
  font-stretch: normal;
  font-style: italic;
  line-height: 1.4;
  letter-spacing: normal;
  color: #222053;
  margin: 0;
}

.spage-wrpcnt-defbtn {
  width: 220px;
  height: 45px;
  border-radius: 10px;
  background-color: #e2a925;
  font-size: 17px;
  color: #222053;
  font-family: AcuminProNormal;
  margin: 20px 0 20px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-decoration: none;
  padding-top: 3px;
}
.spage-wrpcnt-defbtn:hover {
  -webkit-transition: 0.2s;
  transition: 0.2s;
  text-decoration: none;
  color: #4343b7;
  background-color: #f0b327;
}
.spage-wrpcnt-defbtn:active {
  color: #222053;
  background-color: #e2a925;
}

.spage-wrpcnt-rowsec {
  min-width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
