@media (min-width: 1999px) {
  .spage-contfluid {
    background-size: 100% 3%, 100% 14%;
  }
}
@media (max-width: 1750px) {
  .spage-contfluid {
    background-size: 100% 3%, 1800px 15%;
  }
}
@media (max-width: 1350px) {
  .spage-contfluid {
    background-size: 100% 3%, 1400px 15%;
  }
}
@media (max-width: 992px) {
  .spage-wrpcnt-midtxt {
    width: 95%;
  }

  .spage-contfluid {
    background-size: 100% 3%, 1400px 13%;
  }
}
@media (max-width: 769px) {
  .spage-wrpcnt-rowsec-row {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    margin-top: 0;
  }

  .spage-wrpcnt-rowsec-col {
    width: 100%;
    padding-top: 20px;
  }

  .spage-wrpcnt-rowsec-col {
    min-height: auto;
  }

  .spage-contfluid {
    background-size: 100% 3%, 1000px 9%;
  }
}
@media (max-width: 576px) {
  .spage-wrpcnt-maintitle {
    margin: 20px 5px 0 5px;
  }

  .spage-wrpcnt-midtxt {
    width: 100%;
    padding: 20px;
    margin-top: 0;
  }

  .spage-wrpcnt-rowsec-col {
    padding: 20px;
  }

  .spage-wrpcnt-dtit-botttitle {
    width: 100%;
    text-align: left;
    padding: 0 10px 0 20px;
  }

  .spage-contfluid {
    background-size: 100% 3%, 700px 7%;
    background-position: top center, bottom right;
  }
}
@media (max-width: 500px) {
  .spage-wrpcnt-maintitle {
    font-size: 27px;
  }

  .spage-wrpcnt-maintitle::after {
    width: 80%;
  }

  .spage-wrpcnt-deftitle {
    font-size: 23px;
  }

  .spage-wrpcnt-midtxt {
    font-size: 15px;
  }

  .spage-wrpcnt-itsuportrapidtitle {
